export default {
	data() {
		return {
			list: [{
				id: 1,
				name: '手机静音',
				img: '../../static/img/jing.png',
				imgs: '../../static/img/jing1.png',
				flag: true,
			}, {
				id: 2,
				name: '锁定',
				img: '../../static/img/suo.png',
				imgs: '../../static/img/suo1.png',
				flag: false
			}, {
				id: 3,
				name: '传文件',
				img: '../../static/img/wenjian.png',
				imgs: '../../static/img/wenjian1.png',
				flag: false
			}],
			cap1: false,
			cap2: true
		}
	},
	methods: {
		//返回上一页
		getBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		//手机电脑状态改变
		bindChange(e) {
			this.showId = e;
			let tempArr = this.list;
			tempArr.forEach(item => {

				if (item.id == e) {
					item.flag = !item.flag;
				}
			})
			this.list = tempArr;
			if (e == 1) {
				this.cap1 = !this.cap1;
			}
			if (e == 2) {
				this.cap2 = !this.cap2;
			}
		},
		//退出mac
		exit() {
			uni.showModal({
				title: '提示？',
				content: '是否退出 Mac 微信',
				confirmColor: '#3F536E',
				success(res) {
					if (res.confirm) {
						uni.showToast({
							icon: "none",
							title: '退出成功！'
						})
					}
				}
			})
		}
	}
}
